.flip-card {
  /* background-color: transparent; */
  width: 350px;
  height: 200px;
  padding: 5px;
  -webkit-perspective: 1000px;
  perspective: 1000px;
  /* Remove this if you don't want the 3D effect */
}

/* This container is needed to position the front and back side */

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: -webkit-transform 0.8s;
  transition: transform 0.8s;
  transition: transform 0.8s, -webkit-transform 0.8s;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
}

/* Do an horizontal flip when you move the mouse over the flip box container */

.flip-card:hover .flip-card-inner {
  -webkit-transform: rotateY(180deg);
  transform: rotateY(180deg);
}

/* Position the front and back side */

.flip-card-front,
.flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  /* Safari */
  backface-visibility: hidden;
}

/* Style the front side (fallback if image is missing) */

.flip-card-front {
  box-shadow: 4px 2px 22px -1px rgba(131, 129, 129, 0.67);
  -webkit-box-shadow: 4px 2px 22px -1px rgba(131, 129, 129, 0.67);
  -moz-box-shadow: 4px 2px 22px -1px rgba(131, 129, 129, 0.67);
  border-radius: 10px;
  color: black;
  background-color: white;
  /* background-image: linear-gradient(to right, #e4f585, #e5f190, #e5ed9b, #e5eaa5, #e4e6b0, #e4e6b0, #e4e6b0, #e4e6b0, #e5eaa5, #e5ed9b, #e5f190, #e4f585); */
}

/* Style the back side */

.flip-card-back {
  box-shadow: 4px 2px 22px -1px rgba(131, 129, 129, 0.67);
  -webkit-box-shadow: 4px 2px 22px -1px rgba(131, 129, 129, 0.67);
  -moz-box-shadow: 4px 2px 22px -1px rgba(131, 129, 129, 0.67);
  border-radius: 10px;
  background-color: #3F3D56;
  color: white;
  -webkit-transform: rotateY(180deg);
  transform: rotateY(180deg);
}

